import { apiRequest } from './apiClient';

export interface User {
  id: number;
  username: string;
  email: string;
  createdAt: string;
}

export interface AuthResponse {
  success: boolean;
  message: string;
  token: string;
  user: User;
}

export const authService = {
  register: async (username: string, email: string, password: string): Promise<AuthResponse> => {
    return apiRequest<AuthResponse>('/users/register', {
      method: 'POST',
      body: { username, email, password },
    });
  },

  login: async (email: string, password: string): Promise<AuthResponse> => {
    return apiRequest<AuthResponse>('/users/login', {
      method: 'POST',
      body: { email, password },
    });
  },

  logout: async (): Promise<{ success: boolean, message: string }> => {
    return apiRequest('/users/logout', {
      method: 'POST',
    });
  },

  getCurrentUser: async (): Promise<User | null> => {
    try {
      // Si vous avez un endpoint pour obtenir l'utilisateur actuel
      const response = await apiRequest<{ success: boolean, user: User }>('/users/me');
      return response.user;
    } catch (error) {
      return null;
    }
  }
};